<template>
    <div class="home-index">
        <HomeIndexLeftNav
            :leftNavList="$store.state.user.MyPageLeftNavList"
            class="my-page-left"
            :handleRefresh="handleRefresh"
            :handleItem="handleItem"
            :isRefresh="isRefresh"
        />
        <div class="middle-container">
            <transition name="slide">
                <keep-alive>
                    <component :is="component"></component>
                </keep-alive>
            </transition>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isRefresh: false
        };
    },
    watch: {
        $route: {
            handler: function(to, from) {
                //根据key 获取类型
                if (to) {
                    let key = to.query.key;
                    if (!key) return;
                    if (key) {
                        return this.$store.dispatch("setMyPageLeft", key);
                    }
                }
            },
            immediate: true
        }
    },
    computed: {
        component() {
            if (this.$store.state.user.MyPageLeftNavList.length) {
                return this.$store.state.user.MyPageLeftNavList.filter(
                    v => v.isActive
                )[0].component;
            } else {
                return "MyPageBlog";
            }
        }
    },
    methods: {
        handleItem(e) {
            let key = e.target.dataset.key;
            if (key) {
                this.$router.push("/my-page?key=" + key);
            }
        },
        async handleRefresh() {
            if (this.isRefresh) return;
            this.isRefresh = true;
            await this.$store.commit("RESETMYNAV");
            this.$message({
                showClose: true,
                message: "刷新成功",
                type: "success"
            });
            let timer = setTimeout(() => {
                this.isRefresh = false;
                clearTimeout(timer);
            }, 2500);
        }
    }
};
</script>

<style lang="scss" scoped>
.home-index {
    display: flex;
    max-width: 1180px;
    width: 100%;
    margin: 0 auto;
    flex-direction: row;
    position: relative;
    padding-top: 56px;
    margin-bottom: 12px;
    .my-page-left {
        max-width: 100px;
    }
    .middle-container {
        max-width: 1072px;
        width: 100%;
        min-height: 450px;
    }
}
</style>